export default class Sprite_animation {
  constructor(args) {
    this.selector = args.selector;
    this.container = document.querySelector(this.selector);
    this.img = args.img;
    this.img_width = args.img_width;
    this.img_height = args.img_height;
    this.col = args.col;
    this.row = args.row;
    this.scale = args.scale || 1;
    this.classes = args.class ? args.class.split(' ') : '';
    this.speed = args.speed || 50;
    this.offset = args.offset || 0;

    this.init();
  }

  add_container_style() {
    this.container.style.width = `${(this.img_width / this.col)*this.scale}px`;
    this.container.style.height = `${(this.img_height / this.row)*this.scale}px`;
    this.container.style.position = `relative`;
    this.container.style.overflow = `hidden`;
  }

  create_sprite() {
    let sprite = document.createElement('img');
    sprite.style.width = `${100 * this.col}%`;
    sprite.style.height = `${100 * this.row}%`;
    sprite.style.position = `absolute`;
    sprite.style.top = `0`;
    sprite.style.left = `0`;
    sprite.setAttribute('src', this.img);
    sprite.classList.add(`sprite_animation`);

    if ( this.classes[0] != '' ) {
      for (let i = 0; i < this.classes.length; i++) {
        sprite.classList.add(this.classes[i]);
      }
    };

    this.container.appendChild(sprite);
    this.sprite = document.querySelector(`${this.selector} .sprite_animation`);
  }

  animation() {
    let counter = 0;
    let now_col = 0;
    let now_row = 0;
    let iteration = (this.col * this.row) - this.offset;
    let sprite_width = (this.img_width/this.col)*this.scale;
    let sprite_height = (this.img_height/this.row)*this.scale;


    let go_animation = () => {
      setTimeout(() => {

        this.sprite.style.transform = `translate3D(${-sprite_width*now_col}px, ${-sprite_height*now_row}px, 0)`;


        if ( now_col === this.col - 1 ) {
          now_col = 0;
          now_row++;
        } else {
          now_col++;
        }



        counter++;
        if ( counter < iteration ) {
          go_animation();
        }
      }, this.speed);
    };

    go_animation();
  }

  animation_revers() {
    let iteration = (this.col * this.row) - this.offset;
    let counter = iteration;
    let now_col = this.col - this.offset - 1;
    let now_row = this.row - 1;
    let sprite_width = (this.img_width/this.col)*this.scale;
    let sprite_height = (this.img_height/this.row)*this.scale;


    let go_animation = () => {
      setTimeout(() => {

        this.sprite.style.transform = `translate3D(${-sprite_width*now_col}px, ${-sprite_height*now_row}px, 0)`;


        if ( now_col === 0 ) {
          now_col = this.col - 1;
          now_row--;
        } else {
          now_col--;
        }



        counter--;
        if ( counter > 0 ) {
          go_animation();
        }
      }, this.speed);
    };

    go_animation();
  }

  init() {
    this.add_container_style();
    this.create_sprite();
  }
}
