import {selector, animation} from './animation_letter_var';
import ScrollMagic from 'scrollmagic';

window.addEventListener('load', () => {
  let controller = new ScrollMagic.Controller();

  new ScrollMagic.Scene()
    .addTo(controller);

  function letter_animation( selector, animation ) {
    let scene_b = new ScrollMagic.Scene({ triggerElement: selector, duration: 0, offset: 10})
      .on("enter", function (event) {
        animation.animation();
      })
      .on("leave", function (event) {
        animation.animation_revers();
      })
      .addTo(controller);
  }

  letter_animation( '.card__letter-b', animation.b );
  letter_animation( '.card__letter-l', animation.l );
  letter_animation( '.card__letter-m', animation.m );
  letter_animation( '.card__letter-r', animation.r );
  letter_animation( '.card__letter-v', animation.v );
  letter_animation( '.card__letter-c', animation.c );
});

