import Gradient from './Gradient';
import Sprite_animation from './Sprite_animation';
import './animation_letter';


let title_animation = document.querySelector('.hero__lookbook-desktop');

let animation_lookbook = new Sprite_animation({
  selector: '.hero__lookbook-desktop',
  img: './images/sprite/sprite-lookbook.png',
  img_width:1614,
  img_height: 16800,
  col: 2,
  row: 42,
  scale: 1,
  speed: 35
});


let gradient = {
  style: '.design_item-style .design_item__title',
  practical: '.design_item-practical .design_item__title',
  slip_resistant: '.design_item-slip-resistant .design_item__title',
  comfort: '.design_item-comfort .design_item__title',
  support: '.design_item-support .design_item__title',
  refinement: '.design_item-refinement .design_item__title',
};

new Gradient(gradient.style, '#19847B', '#36A056');
new Gradient(gradient.practical, '#5ADAC4', '#5AB9DC');
new Gradient(gradient.slip_resistant, '#549ECB', '#34BFD1');
new Gradient(gradient.comfort, '#F5BAB7', '#F094AF');
new Gradient(gradient.support, '#D39B3F', '#D98150');
new Gradient(gradient.refinement, '#FF3C46', '#EC325D');

window.addEventListener('load', () => {
  animation_lookbook.animation();
});

